ación de productos cárnicos exige el cumplimiento riguroso de prácticas higiénico-sanitarias, controles de calidad y condiciones tecnológicas específicas en cada etapa del proceso, desde la recepción de materias primas hasta la distribución del producto final. La correcta manipulación de la carne, el uso adecuado de ingredientes y aditivos, el mantenimiento de la cadena de frío, la limpieza y desinfección de equipos, así como la aplicación de buenas prácticas por parte del personal, son pilares fundamentales para garantizar alimentos seguros, estables y de alta calidad.")])]),_c('div',{staticClass:"col-lg-5 col-md-9"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t6/slide4.png"),"alt":""}})])])])],1)])])]),_vm._m(3),_c('div',{staticClass:"tarjeta color-primario p-3",attrs:{"data-aos":"fade"}},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_vm._m(4),_c('div',{staticClass:"col"},[_c('div',{staticClass:"row justify-content-between align-items-center"},[_vm._m(5),_c('div',{staticClass:"col-auto mb-3 mb-sm-0"},[_c('a',{staticClass:"boton color-acento-botones texto-blanco",attrs:{"href":_vm.obtenerLink('downloads/Buenas_practicas_industria_carne.pdf'),"target":"_blank","type":"application/pdf"}},[_c('span',[_vm._v("Descargar ")]),_c('i',{staticClass:"fas fa-file-download"})])])])])])])])],1)
